@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;1,100;1,300;1,400;1,700&family=Ubuntu:ital,wght@0,300;0,400;0,700;1,700&display=swap);
:root {
  --white: white;
  --text: #403c4a;
  --text-emphasized: #201d29;
  --text-muted: #78757f;
  --text-pink: #c10b7e;
  --yellow: #ffe742;
  --yellow-dark: #ddc62c;
  --pink: #ff87d4;
  --pink-faded: #f5f2fc;
  --pink-dark: #c10b7e;
  --blue: #87fffb;
  --blue-dark: #63ddd9;
  --black: #171321;
  --gray-light: #d4d0e0;
  --gray-medium: #b1acb9;
  --font-family: jwf, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  --background: var(--white);
  --foreground: var(--black);
  --footnote-hover: var(--pink-dark);
}

* {
  font-family: jwf, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  font-family: var(--font-family);
  -ms-scroll-chaining: chained;
      overscroll-behavior: auto;
  padding: 0px;
  margin: 0px;
}
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  font-family: jwf, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  font-family: var(--font-family);
  font-weight: 400;
  line-height: 1.45;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  max-width: 100vw;
  overflow-x: hidden;
}

.outer {
  color: #403c4a;
  color: var(--text);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #171321;
  color: var(--foreground);
  font-weight: 900;
  line-height: 1.1;
  transition: 200ms color linear;
}

a {
  color: #c10b7e;
  color: var(--text-pink);
  font-weight: 400;
}

strong,
b {
  font-weight: 600;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; /* added line */
}
 .block {
    background: var(--background);
    border-bottom: 1px solid var(--gray-light);
    padding: 5rem 5vw;
    transition: 200ms background linear;
    position: relative;
  }

  .block.yellow {
    background: var(--yellow);
    border-color: var(--yellow-dark);
  }
.hero {
  height: 390px;
  margin: 0 auto;
  max-height: 54vw;
  max-width: 700px;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  width: 90vw;
  z-index: 1;
}

.hero-first-line {
  display: block;
  font-size: clamp(20px, 25.5vw, 120px);
  line-height: 0.95;
  color: var(--black);
  font-weight: 900;
}

.hero-first-line.maximum-boops {
  font-size: clamp(20px, 26.5vw, 120px);
}

.hero-box {
  background: var(--black);
  color: var(--yellow);
  display: block;
  margin: auto;
  text-align: center;
  font-size: clamp(20px, 10.5vw, 120px);
  font-weight: 900;
  line-height: 1;
  padding: 0 0.5rem;
  transition: 200ms background linear;
}

.hero-tagline {
  display: block;
  font-size: 2.42vw;
  font-size: var(--size, 2.42vw);
  margin-top: 0.125rem;
  font-weight: 900;
}

.love {
  background: url('https://res.cloudinary.com/jlengstorf/image/upload/q_auto,f_auto/v1593755650/jason.af/love.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  color: transparent;
  display: inline-block;
  height: 1.2em;
  margin: -0.1em -3px;
  position: relative;
  top: -0.05em;
  top: var(--top, -0.05em);
  transform: scale(1.1) rotate(-11deg) translateY(0.1em);
  transform: scale(var(--scale, 1.1)) rotate(var(--rotation, -11deg)) translateY(0.1em);
  transform-origin: center;
  width: 1.6em;
}

.hero-cycle {
  background: transparent;
  border: none;
  color: var(--text-muted);
  display: block;
  font-size: 0.75rem;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  font-weight: 400;
  letter-spacing: 0.2em;
  margin: 0 auto;
  padding: 0;
  position: relative;
  text-align: center;
  text-decoration: none;
  width: 30px;
  z-index: 20;
}

.hero-cycle.cycle-is-active {
  animation-duration: 500ms;
  animation-name: spin-hero-cycle;
  animation-iteration-count: 1;
}

.hero-cycle img {
  width: 100%;
}

@keyframes spin-hero-cycle {
  from {
    transform: rotate(-1turn);
  }

  to {
    transform: rotate(0);
  }
}

.temp-tagline {
  display: block;
  font-size: 2.42vw;
  margin-top: 0.125rem;
  color: var(--black);
}

.cools {
  mix-blend-mode: overlay;
  z-index: 10;
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; /* added line */
}

